.btn {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
}
.btn_default {
  background-color: #ececec;
  color: #000;
}
.btn_positive {
  background-color: #64c964;
  color: #fff;
}
.btn_negative {
  background-color: #fd565f;
  color: #fff;
}
.btn_login {
  position: absolute;
  top: 1%;
  right: 1%;
}
